body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
}
body{
    width: 100%;
    background-color: #fff;
    font-family: sans-serif;
}
li{
    overflow: hidden;
    font-size: 18px;
}
.white{
    color: #fff;
}
.black{
    color:#000;
}
.container{
    width: 85%;
    margin: 0px auto;
}
.d-flex{
    display: flex;
}
.align-items-center{
    align-items: center;
}
.justify-content-space-between{
    justify-content: space-between;
}
.justify-content-center{
    justify-content: center;
}
h1,h2,h3,h4,h5,h6,span{
    font-size: 18px;
  }
  a{
    text-decoration: none;
    color: #000;
  }
  .row{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
  }
  .col-2{
    width: 50%;
    display: flex;
    padding: 10px;
    align-items: center;
    justify-content: center;
  }
  .col-3{
    width: 33.33%;
    display: flex;
    align-items: center;
    padding: 10px;
    justify-content: center;
  }
  .col-4{
    width: 25%;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 5px 0px;
    justify-content: center;
  }
  .pointer{
    cursor: pointer;
  }
  .px-15{
    padding: 15px;
  }
  .pxy-10{
    padding: 0px 10px;
  }
  .mxy-10{
    margin: 0px 10px;
  }
  .relative{
    position: relative;
  }
  .absolute{
    position: absolute;
  }
  .fixed{
    position: fixed;
  }
  .z-index{
    z-index: 12;
  }
  .d-block{
    display: block;
  }
  .pointer{
    cursor: pointer;
  }
  .pl-5{
    padding-left: 5px;
  }
  .f-400{
    font-weight: 400;
  }
  .p-b{
    padding-bottom: 8px;
  }
  .m-10{
    margin: 10px;
  }
  .pr-10{
    padding-right: 10px;
  }
  .m-8{
    margin: 8px;
  }
  .pbt-30{
    padding: 15px 50px;
  }
  .px-20{
    padding: 0px 20px;
  }
  .f-direction-column{
    flex-direction: column;
  }
  .mx-5{
    margin: 0px 5px;
  }
  .black{
    color: #000;
  }
  .mtb-20{
    margin: 20px 0px;
  }
  .bold-800{
    font-weight: 800;
  }
  .bold-600{
    font-weight: 600;
  }
  .white{
    color: #fff;
  }
  .lh-1{
    line-height: 1;
  }
  .ls-l {
    letter-spacing: -.05em!important;
  }
  .container{
    width: 85%;
    margin: 0px auto;
    overflow: hidden;
  }
  .mxy-30{
    margin-right: 30px;
  }
  .my-30{
    padding: 0px 30px;
  }
  .mxy-15{
    margin: 0px 15px;
  }
.header{
    padding: 5px, 15px;
    position: -webkit-sticky;
    position: sticky;
    width: 100%;
    /* display: block; */
    /* background-color: rgb(216, 127, 127); */
}
.menubar{
    padding: 15px;
    margin: 0 14px;
    /* padding-right: 2em; */
}
.header-middle-items{
    width: 45%;
    /* background-color: bisque; */
}
.input-box{
    width: 100%;
    padding: 0px 20px;
    /* background-color: aqua; */
    /* border-radius: 50%; */
}
input.search-box{
    width: 100%;
    height: 40px;
    border: 1px solid #9bbaca80;
    border-radius: 1.5em;
    margin: 0px 15px;
    padding: 0px 20px;
}
.search-box{
    width: 40px;
    height: 40px;
    right: 1%;
    top: 1%;
    border-left: 1px solid #9bbaca80;
    /* background-color: aqua; */
    font-family: sans-serif;
    font-size: medium;
}
.search-scope{
    background-color: #f1f1f1;
    border-radius: 0 1.5em 1.5em 0;
    height: 39px;
    width: 60px;
}
.search-scope:hover{
    background-color: #e1e1e1;
}
.voice-search{
    margin: 0 10px;
    width: 40px;
    height: 40px;
    /* background-color: aqua; */
    border-radius: 1.5em;
    padding: 8px;
}
.bg-hover:hover{
    background-color: #e1e1e1;
    border-radius: 1.5em;
}
.header-rightbar{
    margin-right: 1.1rem;
}
img.dp{
    border-radius: 1.5em;
    height: 2.2rem;
    width: 2.2rem;
    cursor: pointer;
}
.sidebar{
    position: -webkit-sticky;
    position: sticky;
    display: flex;
    flex-wrap: wrap;
    height: auto;
    width: 100%;
    z-index: 100;
    flex: .2 1;
    /* background-color: aqua; */
}

.active{
    background-color: #e1e1e199;
}
.sidebar-items{
    justify-content: flex-start;
    width: 90%;
    padding: 8px 0px;
    margin: 0 7px;
    /* background-color: blue; */
    border-radius: .7em;
}

.sidebar-items:hover{
    background-color: #d9d9d9;
}

span.sidebar-text{
    font-family: sans-serif;
    font-size: .93em;
    text-align: center;
}

p{
    width: 90%;
    margin: 5px;
    border-bottom: 1px solid #d9d9d9;
}
.filter {
    flex: .8 1;
    /* max-height: 50px; */
    align-items: baseline;
    justify-content: space-around;
    /* margin: 0px 10px; */
    margin-top: 5px;
    border-left: none;
    border-right: none;
    border: 1px  #9e9e9e4d;
    /* background-color: aquamarine; */
    /* width: 100%; */
}

span.filter-items{
    width: auto;
    font-size: .8rem;
    padding: .65rem 1.1rem;
    border-radius: .7em;
    /* border: 1px solid #9e9e9e5c; */
    background-color: #e1e1e199;
    /* margin-top: 5px; */
}
span.filter-items:hover{
    background-color: #d9d9d9;
}
span.filter-items.active{
    background-color: #000;
    border: none;
    color: #fff;

}
.videos{
    flex: .8 1;
}
span.title{
    font-size: 20px;
    padding: 0px 20px;
    line-height: 700;
}
.channelname, .time_description {
    font-family: sans-serif;
    font-size: .9em;
    margin: 3px 1em;
    color: #656565;
    /* background-color: aqua; */
    /* display: block; */
    width: 100%;
}
.seprate{
    padding: 0 10px;
}
.time_options.d-flex {
    width: 100%;
    justify-content: center;
    flex-direction: column;
}

.thumb-img{
    border-radius: .7em;
}

.timestamp {
    font-size: 13px;
}
.videos_duration{
    font-family: 'Arial',sans-serif;
    font-size: .75rem;
    position: absolute;
    border-radius: .5em;
    padding: .2rem .3rem;
    /* bottom: 100%; */
    right: 3%;
    top: 65%;
    background-color: #161616;
    color: #ffffff;
}
.profile_img{
    margin-top: 5px;
}
.description{
    font-family: 'Arial',sans-serif;
    font-size: 1rem;
    font-weight: 600;
    width: 100%;
    margin: 0em .5em;
    padding: 5px 6px;
    /* background-color: antiquewhite; */
}
img.channel_image.pointer {
    width: 37px;
    height: 37px;
    border-radius: 1.5em;
    object-fit: cover;
}
span.videos_duration.absolute {
    font-size: 10px;
}

